import React from "react"
import styles from "./AddNewButton.module.scss"
import classnames from "classnames"
import {ToolbarButtonComponent} from "./fields"
import {Popover, PopoverHeader, PopoverBody} from "reactstrap"
import {PageBuilderContext} from "components/PageBuilder/PageBuilderContext"
import {useUniqueId} from "components/PageBuilder/utility"

export function AddNewButton() {
	const id = useUniqueId()
	const [isOpen, setIsOpen] = React.useState(false)
	const [isHidden, setIsHidden] = React.useState(false)
	const toggle = React.useCallback(() => {
		setIsOpen(_isOpen => {
			if (!_isOpen) {
				setIsHidden(false)
			}
			return !_isOpen
		})
	}, [])
	// TODO fix unmount effect not fireing. possibly happens because of how we create the add button
	// React.useEffect(() => {
	// 	return () => setIsOpen(false)
	// }, [])
	const {elements} = React.useContext(PageBuilderContext)
	return (
		<div>
			<ToolbarButtonComponent id={id} iconLeft={{iconName: "mdiPlusBox"}} tooltip={ss.i18n._t("ZAUBERFISCH_PAGEBUILDER.Add")} />
			{id ? <Popover placement="bottom" target={id} {...{toggle, isOpen}} popperClassName={classnames({[styles.popoverHidden]: isHidden})}>
				<PopoverHeader>{ss.i18n._t("ZAUBERFISCH_PAGEBUILDER.AddDropdownTitle")}</PopoverHeader>
				<PopoverBody>
					<div className={styles.elements}>
						{elements ? Object.entries(elements).map(([key, element]) => {
							if (element.craft.related && element.craft.related.CreateButton) {
								return <div {...{key}}>
									{element.craft.related.CreateButton({
										onCreate: () => setIsOpen(false),
										onDragStart: () => {
											if (navigator.userAgent.toLowerCase().indexOf("firefox") > -1) {
												setIsHidden(true)
											} else {
												// in chrome, hiding the parent element to early will cause the drag to be canceled
												setTimeout(() => setIsHidden(true), 1000)
											}
										},
									})}
								</div>
							}
							return null
						}) : null}
					</div>
				</PopoverBody>
			</Popover> : null}
		</div>
	)
}





